
div.start {
    position: absolute;
    box-sizing: border-box;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 2px solid #000;
    box-shadow: 0 0 26px 10px #000;
    border-radius: 10%;
    text-align: center;
    font-family: 'Righteous', cursive;
    padding: 1%;
    font-family: 10px;
    background-color: rgb(53, 53, 53);
    color: #fff;
}

div.start h1,
div.start h2,
div.start button.start_draw,
div.users span {
    text-transform: uppercase;
    font-family: 'Righteous', cursive;
    text-shadow: 0 0 15px #000;
}

div.players,
div.users {
    padding-top: 3%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

div.players button,
div.users button {
    display: block;
    border: none;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    transition: .3s;
}

div.players span,
div.users span {
    display: block;
    width: 12%;
    border: none;
    background-color: transparent;
    color: #fff;
}

div.start button.start_draw {
    position: absolute;
    display: block;
    left: 50%;
    bottom: 10%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 80%;
    height: 20%;
    background-color: #777;
    border: 6px solid #000;
    border-radius: 20px;
    color: #fff;
    transition: .4s;
}

