nav {
    display: -webkit-flex;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    height: 60px;
    width: 100%;
    background-color: #000;

}

.navigation {
    /* display: flex; */
    top: 0;
    left: 0;
    background-color: #000;
    list-style: none;
}

.btn_nav {
    display: block;
    width: 120px;
    height: 50px;
    margin: 0 1%;
    font-family: 'Righteous', cursive;
    text-transform: uppercase;
    transition: .3s;
    cursor: pointer;
    color: #fff;
    font-size: 20px;
    text-align: center;
    line-height: 60px;
}

.btn_nav:nth-of-type(1) {
    margin-left: 2%;
}

.btn_nav::after {
    content: '';
    display: block;
    position: relative;
    left: 0;
    bottom: 5px;
    border-bottom: 2px solid #fff;
    margin: 0 100%;
    transition: .3s;
}

.btn_nav:hover {
    color: #aaa;
}

.btn_nav:hover::after {
    border-color: #aaa;
    margin: 0;
}

.btn_nav.inactive {
    color: #666;
}

.btn_nav.inactive::after {
    border: none;
}

